import React, { useContext } from 'react';

import Menu from "./Menu";

import {Context} from "./OurContext";

import {useTranslation} from 'react-i18next';


export default function Header(props){


	let ourContext = useContext(Context);

	const {t} = useTranslation();

	ourContext.color = 'blue';

	//const values = ["Valuable competition data made right", "We collect, prepare and process data for you", "Regularly collected valuable infromation for you", "We scrape, we parse, we collect"];
	

	

	

	return (

		<header className="header">
			<Menu/>
			<div className="header__container">

				<div className="header__container__text-block">
					<h1 className="header__container__text-block__title">
					{t('title')}
					</h1>
					
				</div>
				

			</div>
			<div className="header__face"></div>			
		</header> 

		);
}







