import React from 'react';



import HamburgerButton from './HamburgerMenu';
import "./Menu.css";
import {useTranslation} from 'react-i18next';




export default function Menu(){
	const {t} = useTranslation();

	return (

			
			<nav className="mainNav">
			 <ul className="mainMenu">
				

				<li><div id='hamburger'> <HamburgerButton height='3em' 
			 			toggler={(toggle)=>{
			 					
			 						let elems = document.getElementsByClassName("menuItem");
			 						Array.prototype.forEach.call(elems, (elem)=>{elem.classList.toggle('displayMenu')} )
			 						
								
								
									}}/> </div></li>			 	
			
				<li className="menuItem"><a href="#services">{t('menu_item_services')}</a></li>
				<li className="menuItem"><a href="#cases">{t('menu_item_cases')}</a></li>
				<li className="menuItem"><a href="#example">{t('menu_item_example')}</a></li>
			  </ul>
			 </nav>
			)

}

