import React from 'react';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {useTranslation} from 'react-i18next';

const Info = (props)=>{


    const {t} = useTranslation();

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        
      };


    return (
        <div className="info">
            <div className="info__container" id="services">
                <div className="slider">
                <Slider {...settings}>
                    <div>
                        <img src="/assets/data_collection.jpg"  className="slider-image"/>
                        <div className="slider-text"> {t('info_slider_1')}</div>
                       
                    </div>
                    <div>
                        <img src="/assets/excel.jpg" className="slider-image" />
                        <div className="slider-text"> {t('info_slider_2')}</div>
                       
                    </div>
                    <div>
                        <img src="/assets/numbers.jpg" className="slider-image" />
                        <div className="slider-text"> {t('info_slider_3')}</div>
                    </div>
                </Slider>
                
                </div>
                <div className="info__container__text-description">
                    {console.log(t('info_description', {returnObjects : true}).length)}
                    <p>{t('info_description', {returnObjects : true}).map((e)=><p>{e}</p>)}</p>
                    

                </div>
            </div> 

        </div>

    )




};


export default Info;