import React from 'react';

import {useTranslation} from 'react-i18next';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPaperPlane, faDollarSign, faChartLine, faRobot } from '@fortawesome/free-solid-svg-icons'

const UseCases = (props)=>{

    const {t} = useTranslation();



    return (


        <div className='use-cases'>
            <div className='use-cases__container' id="cases">
                <h1>{t('use_cases_header')}</h1>
                <div >
                    <div style={{position:"relative"}}>
                        <div className="use-cases__container__icon" ><FontAwesomeIcon icon={faRobot} /> </div>
                        <h2>{t('use_cases_1_header')}</h2>
                        <p>{t('use_cases_1_body', { returnObjects: true })}</p>
                    </div>

                    <div style={{position:"relative"}}>
                        <div className="use-cases__container__icon" ><FontAwesomeIcon icon={faChartLine} /> </div>
                        <h2>{t('use_cases_2_header')}</h2>
                        <p>{t('use_cases_2_body', { returnObjects: true })}</p>
                    </div>

                    <div style={{position:"relative"}}>
                        <div className="use-cases__container__icon" ><FontAwesomeIcon icon={faDollarSign} /> </div>
                        <h2>{t('use_cases_3_header')}</h2>
                        <p>{t('use_cases_3_body', { returnObjects: true })}</p>
                    </div>

                    <div style={{position:"relative"}}>
                        <div className="use-cases__container__icon" ><FontAwesomeIcon icon={faPaperPlane} /> </div>
                        <h2>{t('use_cases_4_header')}</h2>
                        <p>{t('use_cases_4_body', { returnObjects: true })}</p>
                    </div>
                    

                </div>
            </div>
        </div>



    )


}

export default UseCases;