import React, {useEffect} from 'react';
import {useTranslation} from 'react-i18next';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFileCsv } from '@fortawesome/free-solid-svg-icons'





const TableRow = (props)=>{
    const {rows} = props;
    const [first, second, third, fourth, fifth, sixth] = rows;

    return (

    <tr>
        <td>{first}</td>
        <td>{second}</td>
        <td>{third}</td>
        <td>{fourth}</td>
        <td>{fifth}</td>
        <td>{sixth}</td>
    </tr>

    )

}


const Table = ()=>{

    const {t} = useTranslation();


    return (

        <table cellpadding="7" className="example-table">
            <caption>{t('example_table_caption')}</caption>
           <TableRow rows={[ "by John Baichtal",	"10 LED Projects for Geeks",	"Build Light-Up Costumes, Sci-Fi Gadgets, and Other Clever Inventions",	"July 2018 240 pp.", 	"Full Color"	,"9781593278250"]} />
           <TableRow rows={[ "by Rui Santos and Sara Santos",	"20 Easy Raspberry Pi Projects",	"Toys, Tools, Gadgets, and More!",	"April 2018, 288 pp.",	"Full Color",	"9781593278434"  ]} />
           <TableRow rows={[ "by Max Wainewright",	"25 Scratch 3 Games for Kids",	"A Playful Guide to Coding",	"October 2019, 128 pp.", "4-Color",	"9781593279905"  ]} />
           <TableRow rows={[ "by Cameron Coward",	"A Beginner's Guide to 3D Modeling",	"A Guide to Autodesk Fusion 360",	"June 2019, 152 pp.",	"Full color",	"9781593279264"  ]} />
           <TableRow rows={[ "by Tobias Klein",	"A Bug Hunter's Diary",	"A Guided Tour Through the Wilds of Software Security",	"November 2011, 208 pp.", "", "978-1-59327-385-9"  ]} />
        </table>

    )

}

const Cart = ()=>{

    const {t} = useTranslation();


    useEffect( ()=>{

      

            let options = {threshold: 0.5};

            let callback = (entries, observer)=>{
                entries.forEach( (entry) => {
                    if( entry.isIntersecting){
                        
                        entry.target.classList.add("decrease-shadow");
                        observer.unobserve(entry.target);
                    }
                    

                });


	        }

            let observer = new IntersectionObserver(callback, options);
            const cart = document.getElementsByClassName('example-cart')[0];
            observer.observe(cart);

      



    }, []);

    return (

        <div className="example-cart">
            <a className="example-cart__icon" href="/assets/example.csv">
                <FontAwesomeIcon icon={faFileCsv} size="6x" color="#D3CCE3" className="example-cart__icon-image"/> 
                <div className="example-cart__header">{t('example_cart_header')}</div>
            </a>
            <div className="example-cart__description">{t('example_cart_description')}</div>
        </div>

    )

}


const Example = ()=>{

    const {t} = useTranslation();

    return (

        <div className='example'>
            <div className='example__container' id="example">
                <h1> {t('example_header')} </h1>
                <div className="example__container__data">
                    <Cart/>
                    <div className="example__container__data__price-and-table">
                        <div className="example__container__data__price">
                            <h3>{t('example_list_header')}</h3>
                            <ul>
                                <li>{t('example_list_item_1')}</li>
                                <li>{t('example_list_item_2')}</li>
                                <li>{t('example_list_item_3')}</li>
                                <li>{t('example_list_item_4')}</li>
                                <li>{t('example_list_item_5')}</li>
                                <li>{t('example_list_item_6')}</li>
                                <li>{t('example_list_item_7')}</li>
                            </ul>
                        </div>
                        <Table/>
                        <div className="example__contact-us">
                            <a href="mailto:contact@pocketcubicle.com"><h3 >{t('example_contact_us')}</h3></a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}


export default Example;